.navbarContainer {
  position: fixed;
  top: 0;
  left: 0;
  height: 60px;
  width: 100%;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: solid #efefef 1px;
}

.navbarButtonContainer {
  display: inline-block;
  margin: 0;
}



a.navbarBrand{
  padding: 0px;
  margin-left: 20px;
}
.navbarBrand> img {
  height:40px;
}

.navbarBrand:hover {
  text-decoration:none;
}
.navbarBrand {
  font-variant: small-caps;
  padding: 0px;
}
.singleButtonContainer {
  display: inline-block;
}

.navbarButton, .navbarBrand {
  padding: 20px;
  display: inline-block;
  color:#858585;
}

.navbarButton:hover, .navbarBrand:hover {
  text-decoration: none;
  color:#444444;
}

.navbarButton:focus, .navbarBrand:focus {
  text-decoration: none;
  color:#444444;
}

@media screen and (max-width: 600px) {
	.navbarContainer {
     font-size: 13px;
	}

	.navbarBrand {
     padding-left: 3px;
	}

	.navbarButton {
	  padding: 1em;
	}

   .navbarButton:hover, .navbarBrand:hover {
	  background-color: #e6e6e6;
	}
}

.twitterLogin {
  margin-right: 1.5em;
}
@media screen and (max-width: 350px) {
	.navbarContainer {
      -ms-flex-pack: center;
          justify-content: center;
	}

	.navbarBrand {
     display: none;
	}
}



